'use client';

import { useState } from 'react';

interface EmotionFormProps {
  onSubmit: (text: string) => Promise<void>;
  isLoading: boolean;
}

export default function EmotionForm({ onSubmit, isLoading }: EmotionFormProps) {
  const [reflection, setReflection] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (reflection.trim()) {
      await onSubmit(reflection);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="w-full">
      <div className="mb-4">
        <label htmlFor="reflection" className="block text-sm font-medium text-gray-300 mb-2">
          Share your thoughts or feelings
        </label>
        <textarea
          id="reflection"
          rows={4}
          className="w-full px-3 py-2 text-white border bg-gray-800 border-gray-600 rounded-lg 
                    focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent
                    transition-all duration-200 placeholder-gray-500"
          placeholder="Example: I feel nervous about my first job interview..."
          value={reflection}
          onChange={(e) => setReflection(e.target.value)}
        />
      </div>
      <button
        type="submit"
        disabled={isLoading}
        className={`w-full bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-lg
                   transform transition-all duration-200 hover:scale-[1.02] active:scale-[0.98]
                   ${isLoading ? 'opacity-50 cursor-not-allowed' : 'hover:shadow-lg'}`}
      >
        <span className="flex items-center justify-center">
          {isLoading ? (
            <>
              <svg className="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
              Analyzing...
            </>
          ) : (
            'Analyze My Emotion'
          )}
        </span>
      </button>
    </form>
  );
}
