'use client';

import { useState } from 'react';
import EmotionForm from '@/components/EmotionForm';
import ResultCard from '@/components/ResultCard';
import ErrorMessage from '@/components/ErrorMessage';

// Types for our emotion analysis response
interface EmotionResponse {
  emotion: string;
  description: string;
  confidence: number;
  color: string;
  matched?: boolean;
}

export default function Home() {
  const [isLoading, setIsLoading] = useState(false);
  const [result, setResult] = useState<EmotionResponse | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleAnalyzeEmotion = async (text: string) => {
    if (!text.trim()) {
      setError('Please enter some text to analyze');
      return;
    }

    setIsLoading(true);
    setError(null);
    
    try {
      const response = await fetch('http://localhost:8000/analyze', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ text }),
      });

      if (!response.ok) {
        throw new Error('Failed to analyze emotion');
      }

      const data = await response.json();
      setResult(data);
    } catch (err) {
      console.error('Error analyzing emotion:', err);
      setError('Failed to analyze your reflection. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <main className="flex min-h-screen flex-col items-center p-6 sm:p-24 bg-gray-900 text-white">
      <div className="z-10 w-full max-w-md">
        <h1 className="text-3xl font-bold text-center mb-8 bg-gradient-to-r from-blue-400 to-purple-500 bg-clip-text text-transparent">
          Emotion Reflection Tool
        </h1>
        
        <div className="bg-gray-800 rounded-xl shadow-2xl p-6 w-full border border-gray-700">
          <EmotionForm onSubmit={handleAnalyzeEmotion} isLoading={isLoading} />

          {error && <ErrorMessage message={error} />}
          {result && <ResultCard result={result} />}
        </div>

        <p className="mt-6 text-center text-sm text-gray-400">
          This is a demo application with keyword-based emotion analysis.
        </p>
      </div>
    </main>
  );
}
