'use client';

interface EmotionResponse {
  emotion: string;
  description: string;
  confidence: number;
  color: string;
  matched?: boolean;
}

interface ResultCardProps {
  result: EmotionResponse;
}

export default function ResultCard({ result }: ResultCardProps) {
  const handlePositiveFeedback = () => {
    alert("Thank you for your feedback!");
  };

  const handleNegativeFeedback = () => {
    alert("We'll use your feedback to improve our analysis.");
  };

  return (
    <div 
      className="mt-6 p-5 rounded-lg shadow-md transition-all duration-300 transform animate-fadeIn"
      style={{ backgroundColor: `${result.color}15`, borderLeft: `4px solid ${result.color}` }}
    >
      <div className="flex justify-between items-center mb-3">
        <h3 className="text-lg font-bold" style={{ color: result.color }}>
          {result.emotion}
        </h3>
        <span className="text-sm px-2 py-1 rounded-full bg-gray-700 text-gray-200">
          {Math.round(result.confidence * 100)}% confidence
        </span>
      </div>
      
      <p className="text-gray-300 mb-4">{result.description}</p>
      
      {/* Divider */}
      <div className="border-t border-gray-700 my-3"></div>
      
      {/* Feedback buttons */}
      <div className="flex items-center justify-between">
        <p className="text-sm text-gray-400">Was this analysis accurate?</p>
        <div className="flex gap-2">
          <button 
            onClick={handlePositiveFeedback}
            className="text-xs bg-gray-800 border border-green-600 text-green-500 px-3 py-1 rounded-full
                      hover:bg-green-900 hover:text-green-300 transition-all duration-200 transform
                      hover:scale-105 active:scale-95"
          >
            üëç Yes
          </button>
          <button 
            onClick={handleNegativeFeedback}
            className="text-xs bg-gray-800 border border-red-600 text-red-500 px-3 py-1 rounded-full
                      hover:bg-red-900 hover:text-red-300 transition-all duration-200 transform
                      hover:scale-105 active:scale-95"
          >
            üëé No
          </button>
        </div>
      </div>
    </div>
  );
}
